/* src/components/AboutMe.module.css */

:root {
  --max-width: 600px;
  --padding-large: 20px;
  --padding-small: 10px;
  --gap-large: 20px;
  --gap-small: 10px;
  --breakpoint-tablet: 768px;
  --breakpoint-mobile: 480px;
  --font-size-title: 2em;
  --font-size-description: 1.1em;
  --font-size-price: 1.5em;

  /* Font Sizes for Small Screens */
  --font-size-title-small: 1.8em;
  --font-size-description-small: 1em;
  --font-size-price-small: 1.2em;
}

.aboutMeContainer {
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center;     /* Center vertically */
  min-height: 100vh;       /* Ensure full viewport height */
  border-radius: 2%;
  padding: var(--padding-large);
  box-sizing: border-box;
  flex-direction: column;  /* Stack content vertically */
}

.content {
  text-align: center;
  max-width: var(--max-width); /* Maintain readability */
  width: 100%;                /* Full width of container */
}

.profileImage {
  width: 100%;                /* Full width of container */
  max-width: 400px;           /* Limit maximum width */
  height: auto;               /* Maintain aspect ratio */
  object-fit: cover;
  margin-top: 20px;
  margin-bottom: 20px;
  border: 1px solid black;    /* Correct border syntax */
  border-radius: 1.2px;         /* Optional: Rounded corners */
}

h1 {
  margin-bottom: 10px;
  font-size: var(--font-size-title);
  color: #000000;
}

p {
  font-size: var(--font-size-description);
  color: #000000;
  line-height: 1.6;
}

.linkText {
  /* Additional styling if needed */
  font-size: large;
  font-family: "Playfair Display", system-ui;
}

/* Media Queries for Responsiveness */

/* Tablet and below */
@media (max-width: var(--breakpoint-tablet)) {
  .aboutMeContainer {
    padding: var(--padding-small); /* Reduce padding */
  }

  .profileImage {
    max-width: 300px; /* Reduce image size */
    margin-top: 15px;
    margin-bottom: 15px;
  }

  h1 {
    font-size: var(--font-size-title-small);
  }

  p {
    font-size: var(--font-size-description-small);
  }
}

/* Mobile and below */
@media (max-width: var(--breakpoint-mobile)) {
  .profileImage {
    max-width: 250px; /* Further reduce image size */
    margin-top: 10px;
    margin-bottom: 10px;
  }

  h1 {
    font-size: 1.6em; /* Alternatively, use a variable */
  }

  p {
    font-size: 0.95em;
  }
}
