/* Global styles for the app */

html, body {
    margin: 0;

    padding: 0;
    height: 100%;
    /* font-family: "Playfair Display", system-ui; */
    font-optical-sizing: auto;
    font-style: normal;
    font-weight: 2;
    /* background: */
    /* radial-gradient(ellipse at 20% 20%, #35234b 0%, transparent 70%),
    radial-gradient(ellipse at 60% 20%, #2975bf 0%, transparent 70%),
    radial-gradient(ellipse at 100% 20%, #3d54b1 0%, transparent 70%),
    radial-gradient(ellipse at 100% 100%, #9f3c54 0%, transparent 70%),
    radial-gradient(ellipse at 20% 100%, #362d6f 0%, transparent 70%); */
    background:
    radial-gradient(ellipse at 20% 20%, #8c8d8c 0%, transparent 70%),
    radial-gradient(ellipse at 60% 20%, #838383 0%, transparent 70%),
    radial-gradient(ellipse at 100% 20%, #f8f8f8 0%, transparent 70%),
    radial-gradient(ellipse at 100% 100%, #b6b6b6 0%, transparent 70%),
    radial-gradient(ellipse at 20% 100%, #362d6f 0%, transparent 70%);
  }
  
  .main-container {
    min-height: 100vh; /* Ensures the app takes the full height of the viewport */
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  /* You can also set global link styles */
  a {
    text-decoration: none;
    color: inherit;
  }
  
  /* Global heading styles */
  h1, h2, h3 {
    margin: 0;
    padding: 0;
  }
  
  /* Utility classes */
  .centered {
    text-align: center;
  }
  

  /* src/App.css */
.app-container {
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    margin-bottom: 50px;
    height: 100vh;

  }
  
  .content-container {
    flex: 1;
    padding: 20px;
    overflow-y: auto;
    
  }


  /* Add this to ensure the footer is only visible when needed */
footer {
    position: sticky;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  