/* Grid Container */
.gridContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px; /* Space between the cards */
  padding: 12px;
  margin: 0 auto;
  max-width: 1300px; /* Max width for the grid to not take full screen */
}

/* Product Card */
.productCard {
  background-color: black;
  width: 400px; /* Fixed width for each card */
  padding: 1.2px;
  text-align: center;
  border-radius: 3px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Black shadow around each card */
  transition: transform 0.3s ease-in-out;
}

.productCard:hover {
  transform: scale(1.05); /* Slight zoom on hover */
}

/* Product Image */
.productImage {
  width: 100%;
  height: auto;
  border-radius: 2px;
}
