/* src/components/Header.module.css */

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; /* Full width for horizontal header */
  height: 60px; /* Fixed height for the header */
  background-color: #131314;
  padding: 0 20px; /* Horizontal padding */
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  z-index: 1000;
  color: #ecf0f1;
  border-width: 10px;
  border-color: #775374;
  display: flex;
  flex-direction: row;
  align-items: center; /* Vertically center items */
  justify-content: space-between; /* Space between left and right sections */
}

.leftSection, .rightSection {
  display: flex;
  align-items: center;
}

.leftSection {
  /* Additional styling if needed */
  color: #b2b2b2;
}

.rightSection {
  /* Additional styling if needed */
  padding-right: 50px;
}

.menuItem {
  display: flex;
  align-items: center;
  justify-content: center; /* Center content */
  padding: 8px 12px; /* Padding for clickable area */
  text-decoration: none;
  color: #ecf0f1;
  font-size: 16px; /* Adjusted font size for clarity */
  transition: background-color 0.2s, color 0.2s;
  border-radius: 4px;
}

.menuItem:hover,
.menuItem.active {
  color: #adadad;
}

.icon {
  margin-right: 8px; /* Space between icon and text */
  font-size: 20px; /* Adjust icon size as needed */
}

/* Focus styles for accessibility */
.menuItem:focus {
  outline: 2px solid #81658a; /* Visible focus outline */
  outline-offset: 2px;
}

/* Optional: Adjust link text styling */
.linkText {
  /* You can add additional styling here if needed */
  font-size: x-large;
  font-family: "Playfair Display", system-ui;
}

.AboutMeText {
  font-size: large;
  font-family: "Playfair Display", system-ui;
}
